import { Link } from 'react-router-dom';
import React, { Component } from 'react';
import logo from './logo.png';

class App extends Component {
  constructor(props) {
    super();
  }

  render() {
    return (
      <div>
        <nav className="navbar navbar-transparent navbar-top" role="navigation">
          <div className="container">
            <div className="navbar-header">
              <button id="menu-toggle" type="button" className="navbar-toggle" data-toggle="collapse" data-target="#example">
              <span className="sr-only">Toggle navigation</span>
              <span className="icon-bar bar1"></span>
              <span className="icon-bar bar2"></span>
              <span className="icon-bar bar3"></span>
              </button>
              <Link to='/'>
                <div className="logo-container">
                  <div className="logo">
                    <img src={logo} alt="BibleQuote Logo" />
                  </div>
                  <div className="brand">BibleQuote</div>
                </div>
              </Link>
            </div>
            <div className="collapse navbar-collapse" id="example" >
              <ul className="nav navbar-nav navbar-right">
                <li>
                  <Link to='/repository'>
                    <i className="fa fa-download"></i>
                    Modules
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        <div className="parallax filter-gradient blue" data-color="blue">
          <div className= "container">
          { this.props.children }
          </div>
        </div>
        <footer className="footer">
          <div className="container">
            <nav className="pull-left">
              <ul>
                <Link to='/'>
                  Home
                </Link>
                <Link to='/repository'>
                  <i className="fa fa-download"></i>
                  Modules
                </Link>
              </ul>
            </nav>
            <div className="copyright">
              &copy; 2017-2018 <Link to='#'>BibleQuote.org</Link>
            </div>
          </div>
        </footer>
      </div>
    );
  }
}

export default App;



