import { Link } from 'react-router-dom';
import React, { Component } from 'react';
import './Repository.css';

class Repository extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modules: [],
    };
  }

  componentWillMount() {
    fetch('https://api.github.com/repos/BibleQuote/BibleQuote-Modules/contents')
    .then(response => {
      return response.json();
    })
    .then(modules => {
      this.setState({modules});
    });
  }

  render() {
    return (
      <div className="row repository">
        <div className="col-md-12 main">
          <input type="text" onChange={(event) => {
            const value = event.target.value;
            if (value) {
              const modules = this.state.modules.filter(module => {
                return module.name.includes(value);
              });
              this.setState({modules});
            }
          }}/>
          {
            this.state.modules.map((module, index) => {
              if (module.name.includes('.zip')) {
                return (
                  <ul>
                    <li key={module.name}>
                      <img src={`http://lorempixel.com/100/100/people/${index + 1}/`} />
                      <h3>{module.name.replace('.zip', '')}</h3>
                      <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent euismod ultrices ante, ac laoreet nulla vestibulum adipiscing. Nam quis justo in augue auctor imperdiet.</p>
                      <a href={module.download_url}></a>
                    </li>
                  </ul>
                );
              }
            })
          }
        </div>
      </div>
    );
  }
}

export default Repository;



